.player-control {
    position: absolute;
    bottom: 0;
    width: 100%;
    display: flex;
    height: 70px;
    background-color: #11131E;
    align-items: center;
}
.player-control__play {
    margin: 0 20px 0 20px;
}
.player-control__volume {
    margin-right: 20px;
    display: flex;
}
.volume-down {
    margin-right: 5px;
}
.volume-up {
    margin-left: 5px;
}
.player-control__music-time {
    margin-right: 20px;
    color:rgb(120,123,162);
    font-size:20px;
    position: relative;
}
.player-control__music-time:after {
    content: '';
    position: absolute;
    top:7px;
    right: -15px;
    width: 1px;
    height: 20px;
    background-color: rgb(120,123,162);
}
.player-control__track-name {
    margin-left: 10px;
    color:rgb(120,123,162);
}