.playlist {
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    width: 40%;
    background-color: #222336;
    padding: 0 16px 0 16px;
}
.icon-search {
    margin-right: 5px;
}
.search-music {
    display: flex;
    align-items: center;
    margin-top: 10px;
    border-bottom: 1px solid rgba(0,0,0,0.3);
    padding-bottom: 5px;
}
.search-music input {
    width: 100%;
    background-color: #222336;
    outline: none;
    border: none;
    color:#fff;
    font-size: 16px;
}
.music-list {
    height: 84%;
    margin-top: 5px;
    padding-top: 5px;
    border-top: 1px solid rgba(0,0,0,0.3);
    font-family: 'Roboto Condensed';
    font-size:19.2px;
    overflow-y: auto;
}
::-webkit-scrollbar {
    width: 12px;
}

/* Track */
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    -webkit-border-radius: 10px;
    border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    -webkit-border-radius: 10px;
    border-radius: 10px;
    background: whitesmoke;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
}
::-webkit-scrollbar-thumb:window-inactive {
    background: whitesmoke;
}
.music {
    display: flex;
    justify-content: space-between;
}
.music__photo {
    width: 70px;
    height: 70px;
    background-size:cover;
    border-radius: 50px;
}
.music__description {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.music__group-name {
    text-align: center;
    margin-bottom: 10px;
}
.music__track-name {
    text-align: center;
    font-size: 12.96px;
}