.player-content {
    display: flex;
    margin: 50px 0 0 30px;
}
.photo-track {
    width: 100px;
    height: 100px;
    background-size:cover;
    border-radius: 50px;
}
.description {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 20px;
}
.description__group-name {
    text-align: center;
    color:#787BA2;
    margin-bottom: 10px;
}
.description__track-name {
    text-align: center;
    color:rgb(245,245,245);
}