.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color:rgba(18,19,33,255);
}
.logo {
    color: crimson;
    font-size:28px;
    padding: 10px;
}
.menu-burger {

}
.btn-menu {
    position: absolute;
    top: 4%;
    left:95%;
    width: 30px;
    height: 30px;
    transition: all 300ms cubic-bezier(.61, .01, .42, 1);
    cursor: pointer;
    background: transparent;
    border: 0;
}
.btn-menu .burger__piece {
    height: 0;
    border: 1.5px solid #fff;
    width: 22px;
    display: block;
    position: absolute;
    transition: all 300ms cubic-bezier(.61, .01, .42, 1);
    background:#fff;
}
.btn-menu:hover {
    transition-delay: 100ms;
    transform: scale(1.1);
}
.btn-menu:hover .burger__piece:nth-child(3):before {
    width: 2px;
    height: 2px;
    border-radius: 50%;
    background: #F26451;
}
.btn-menu:hover .burger__piece {
    border: 2px solid #fff;
    height: 9px;
    border-radius: 50%;
    margin-left: -1px;
    margin-top: 2px;
    animation: atom 300ms linear 1;
    width: 25px;
    top: 0;
    background:transparent;
}
.btn-menu:focus {
    outline: 0;
}
.burger__piece:nth-child(1) {
    top: 0;
}

.burger__piece:nth-child(2) {
    top: 8px;
}

.burger__piece:nth-child(3) {
    top: 16px;
}
.btn-menu:hover .burger__piece:nth-child(3):before {
    opacity: 1;
    transition: opacity 600ms cubic-bezier(.61, .01, .42, 1);
}
.btn-menu:active:hover div:nth-child(3):before,
.btn-menu:active div:nth-child(3):before,
.btn-menu:active div:nth-child(2) {
    opacity: 0;
    transition: all 200ms;
}

.btn-menu:active .burger__piece {
    border: 1.5px solid #fff;
    height: 0px;
    border-radius: 0%;
    margin-left: -1px;
    margin-top: 6px;
    animation: division 300ms linear 1;
    width: 25px;
    top: 0px;
}

.btn-menu:active .burger__piece:nth-child(2) {
    width: 0px;
}

.btn-menu:active .burger__piece:nth-child(3) {
    transform: rotate(45deg);
}

.btn-menu:active .burger__piece:nth-child(1) {
    transform: rotate(-45deg);
}

@keyframes atom {
    0% {
        transform: rotate(180deg);
    }
}

@keyframes division {
    0% {
        transform: rotate(180deg);
    }
}

@keyframes ball {
    0% {
        left: -20%;
        top: 10%;
    }
    10% {
        left: 10%;
        top: -35%;
    }
    25% {
        left: 45%;
        top: -50%;
    }
    40% {
        left: 80%;
        top: -20%;
    }
    50% {
        left: 98%;
        top: 18%;
    }
    60% {
        left: 80%;
        top: 50%;
    }
    75% {
        left: 45%;
        top: 80%;
    }
    90% {
        left: 0%;
        top: 60%;
    }
    100% {
        left: -20%;
        top: 10%;
    }
}